import React, { useState } from "react";
import Header from "./Header";
import Footer from "./Footer";
import CreateNode from "./CreateNode";
import Note from "./Note"


const GoogleApi = ()=>{
    const [insert,setInsert]=useState([])
    const passNote=(note)=>{
        // alert("click Here ")
        setInsert((priesD)=>{
                return[...priesD,note]
        })
    }
    const onDlete=(id)=>{
        setInsert((oldData)=>{
            oldData.filter((carent,indx)=>{
                    return indx !== id;
            })
        })
    }
    return(
        <>
        <div className="main1">
            <Header/>
           
            <CreateNode passNote1={passNote} />
            
            {
                insert.map((val,index)=>{
                    return(
                        <Note 
                            key={index}
                            id={index}
                            title={val.title}
                            note={val.note}
                           deletetItem={onDlete}
                        />
                    )
                })
            }
            <Footer/>
            </div>
        </>
    );
}
export default GoogleApi;