import React, { useEffect, useState } from "react";
import axios from "axios"
const Pokymap=()=>{
const[num,setNum]=useState();
const[name, setName]=useState();
const[moves,setMoves]=useState()
const clickHere=(event)=>{
    setNum(event.target.value)
}
useEffect(()=>{
    
    async function getData(){
        const reg=await axios.get(`https://pokeapi.co/api/v2/pokemon/${num}`);
        console.log(reg);
        //console.log(reg.data.name)
        setName(reg.data.name);
        setMoves(reg.data.moves.length)
    }
    getData();
})


    return(
        <>
        <h2>Show the  Num <span style={{color:"red"}}> {num}</span></h2>
        <h2>Show the Name <span style={{color:"red"}}> {name}</span></h2>
        <h2>Show the Moves <span style={{color:"red"}}> {moves} </span></h2>
        
        
            <select value={num} onChange={clickHere}>
                
                <option value={1}>1</option>
                <option value={25}>25</option>
                <option value={3}>3</option>
                <option value={4}>4</option>
                <option value={5}>5</option>
            </select>
        </>
    )
}
export default Pokymap;