import React, { useEffect, useState } from "react";
import "../node_modules/bootstrap/dist/css/bootstrap.min.css"




const Covid=()=>{
    const [data,setdata]=useState([]);
    const getCovidData = async()=>{
        const rej= await fetch("https://data.covid19india.org/data.json");
        const Rejelt=await rej.json();
        console.log(Rejelt.statewise);
        setdata(Rejelt.statewise)
    }
   

    useEffect(()=>{
         getCovidData();
    },[])
    return(
        <>
        <div className="container">
            <div className="row">

          
        
            <h1 className="text-center"><span className="fw-bold">Indian Covid Data</span></h1>
            <div className="table-responsive">
                 <table className="table table-bordered table-hover">
                    <thead className="bg-dark text-white">
                    <tr>
                        <th>State</th>
                        <th>Active</th>
                        <th>confirmed</th>
                        <th>deaths</th>
                        <th>deltaconfirmed</th>
                        <th>recovered</th>
                        </tr>
                    </thead>
                    <tbody>
                   
                    
                    {
                        data.map((carent,ind)=>{
                            return(
                                <tr key={ind}>
                                    <td>{carent.state}</td>
                                    <td>{carent.active}</td>
                                    <td>{carent.confirmed}</td>
                                    <td>{carent.deaths}</td>
                                    <td>{carent.deltaconfirmed}</td>
                                    <td>{carent.recovered}</td>
                                   
                                </tr>
                            )
                        })
                    }
                        
                        
                    </tbody>
                </table> 
            </div>
            </div>
            </div>
        </>
    )
}
export default Covid