import React, { useState } from "react";
import Button from '@mui/material/Button';
import AddIcon from '@mui/icons-material/Add';
const CreateNode=(props)=>{
    const [entValue,setEnter]=useState({ 
        title:"",
        note:""
    }
    );
   
    const enterTitle=(event)=>{
        const value=event.target.value;
        const name= event.target.name;
        setEnter((pre)=>{
            return{
               ...pre,
                [name]:value

            }
           
        })
    }
    const submitValue=(event)=>{
            event.preventDefault() ;
            props.passNote1(entValue);
            setEnter({
                title:"",
                note:""
            })
    }
    return(
        <>
            <div className="main_note">
                <form onSubmit={submitValue}>
                    <input type="text" placeholder="title" name="title" value={entValue.title} onChange={enterTitle} />
                    <textarea rows="" column="" name="note" placeholder="Write A Note..." value={entValue.note}  onChange={enterTitle}></textarea>
                    <Button className="MuiButton-root" type="submit"><AddIcon className="plus_sign"/></Button>
                </form>
            </div>
        </>
    );
}
export default CreateNode;