import React from "react";

import DeleteIcon from '@mui/icons-material/Delete';
const Note=(props) =>{
     const deleteNote=()=>{
        props.deletetItem(props.id)
     }
    
    return(
        <>
            <div className="note">
            <h1>{props.title}</h1>
          <br/>
            <p>{props.note}</p>
            <button className="btn"  onClick={deleteNote}><DeleteIcon/></button>
            </div>
        </>
    );

}
export default Note;